package com.example.redwood.counter.compose

import androidx.compose.runtime.Composable
import app.cash.redwood.Modifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.compose.RedwoodComposeNode
import app.cash.redwood.compose.WidgetNode
import com.example.redwood.counter.widget.Button
import com.example.redwood.counter.widget.SchemaWidgetFactoryProvider
import kotlin.Boolean
import kotlin.OptIn
import kotlin.String
import kotlin.Unit

@Composable
@OptIn(RedwoodCodegenApi::class)
public fun Button(
  text: String?,
  enabled: Boolean = true,
  onClick: (() -> Unit)?,
  modifier: Modifier = Modifier,
) {
  RedwoodComposeNode<SchemaWidgetFactoryProvider<*>, Button<*>>(
      factory = { it.Schema.Button() },
      update = {
        set(modifier, WidgetNode.SetModifiers)
        set(text) { recordChanged(); widget.text(it) }
        set(enabled) { recordChanged(); widget.enabled(it) }
        set(onClick) { recordChanged(); widget.onClick(it) }
      },
      content = {
      },
      )
}
