package com.example.redwood.counter.widget

import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactory
import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactoryProvider
import app.cash.redwood.widget.Widget
import kotlin.Any
import kotlin.OptIn
import kotlin.experimental.ExperimentalObjCName
import kotlin.native.ObjCName

@OptIn(ExperimentalObjCName::class)
@ObjCName(
  "SchemaWidgetFactories",
  exact = true,
)
public class SchemaWidgetFactories<W : Any>(
  override val Schema: SchemaWidgetFactory<W>,
  override val RedwoodLayout: RedwoodLayoutWidgetFactory<W>,
) : SchemaWidgetFactoryProvider<W>

public interface SchemaWidgetFactoryProvider<W : Any> : Widget.Provider<W>,
    RedwoodLayoutWidgetFactoryProvider<W> {
  public val Schema: SchemaWidgetFactory<W>
}
